/* Miscellaneous styles
 ******************* */
 body {
     margin: 0;
     padding: 0;
 }
 input:focus,
 select:focus,
 textarea:focus,
 button:focus {
     outline: none;
 }
 
 h2 {
   margin: 0 0 1rem;
   padding: 0;
 }
 
 
 /* <App /> styles
  ************* */
 .App {
   color: Black;
   font-family: 'Gudea', sans-serif;
   text-align: left;
 }
 
 
 /* <header> styles
  ************** */
 header {
   background-color: Gainsboro;
   padding: 1rem 0;
   width: 100%;
 }
 
 
 /* <nav> styles
  *********** */
 nav {
 }
 nav img {
   display: inline-block;
   float: left;
   height: auto;
   max-height: 4rem;
   max-width: 4rem;
   width: 100%;
 }
 nav h1 {
   display: inline-block;
   float: left;
   font-size: 2rem;
   line-height: 4rem;
   margin: 0;
   padding: 0 0 0 1rem;
 }
 nav .search {
   border: 1px solid Gray;
   border-radius: .25rem;
   clear: both;
   float: none;
   margin: 5rem 0 0;
 }
   nav .search img {
     display: none;
   }
   nav .search input[type=text] {
     border: none;
     border-radius: .25rem;
     display: block;
     float: none;
   }
     nav .search input[type=text]::placeholder {
       color: LightGray;
     }
 /* <nav> styles for screens larger than 576px */
 @media (min-width: 576px) {
 
 }
 /* <nav> styles for screens larger than 768px */
 @media (min-width: 768px) {
   nav .search {
     clear: none;
     float: right;
     margin: 1rem 0;
   }
     nav .search img {
       background-color: Silver;
       border-bottom-right-radius: .25rem;
       border-top-right-radius: .25rem;
       display: inline;
       float: right;
       height: auto;
       max-height: 2rem;
       max-width: 2rem;
       padding: .375rem;
       width: 100%;
     }
     nav .search input[type=text] {
       border-bottom: none;
       border-left: none;
       border-right: 1px solid Gray;
       border-top: none;
       border-bottom-left-radius: .25rem;
       border-bottom-right-radius: 0;
       border-top-left-radius: .25rem;
       border-top-right-radius: 0;
       display: inline;
       width: 200px;
     }
 }
 /* <nav> styles for screens larger than 992px */
 @media (min-width: 992px) {
 
 }
 /* <nav> styles for screens larger than 1200px */
 @media (min-width: 1200px) {
 
 }
 
 
 /* <Calculator /> styles
  ******************** */
 .measurement-icon {
   display: inline-block;
   height: auto;
   margin-left: auto;
   max-height: 4rem;
   max-width: 4rem;
   vertical-align: middle;
   width: 100%;
 }
 .measurement-name {
   display: inline-block;
   font-size: 2rem;
   line-height: 4rem;
   margin: 0;
   padding: 0 0 0 1rem;
   vertical-align: middle;
 }
 fieldset {
   background-color: WhiteSmoke;
   border: 1px solid LightGray;
   border-radius: .25rem;
   margin: 0;
   padding: .75rem;
 }
 input {
   background-color: White;
   border: 1px solid LightGray;
   border-radius: .25rem;
   font-size: .875rem;
   line-height: 1rem;
   padding: .5rem;
   text-align: left;
   width: 100%;
 }
 legend {
   background-color: White;
   border: 1px solid LightGray;
   border-radius: .25rem;
   font-size: 1rem;
   font-weight: 700;
   padding: .375rem;
   width: auto;
 }
 
 
 /* <Accordion /> styles
 ************** */
 .accordion {
 padding: 0 .75rem;
 width: 100%;
 }
 .accordion * {
 vertical-align: middle;
 }
 .accordion > img {
 background-color: CornflowerBlue;
 border-radius: .25rem;
 display: inline-block;
 height: auto;
 max-height: 1.25rem;
 max-width: 1.25rem;
 padding: .25rem;
 width: 100%;
 }
 
 .panel-heading {
 display: inline-block;
 font-size: .75rem;
 line-height: 1.25rem;
 padding: .375rem 0 .375rem .5rem;
 text-align: left;
 width: auto;
 }
 .panel-heading:after {
   content: '\25B2';
   color: Black;
   font-size: .625rem;
   padding-left: .375rem;
 }
 
 .active {
 
 }
   .active:after {
   content: "\25BC";
   }
 
 .panel-body {
 overflow: scroll;
 padding: 0 .5rem;
 transition: max-height 0.2s ease-out;
 width: auto;
 }
 .panel-body a {
   font-size: .875rem;
   margin: 0;
   padding: .5rem 0 0;
 }
   .panel-body a:active, a:link, a:visited {
     color: CornflowerBlue;
   }
   .panel-body a:hover {
     color: RebeccaPurple;
   }
 .panel-body dl {
   display: flex;
   flex-flow: row wrap;
   font-size: .875rem;
   width: 100%;
 }
 .panel-body dt {
   flex-basis: 100%;
   font-weight: 700;
   margin: 0;
   padding: .5rem;
 }
 .panel-body dd {
   flex-basis: 100%;
   font-weight: 400;
   margin: 0 0 0 1.5rem;
   padding: .5rem;
 }
   .panel-body dd > img {
   max-height: auto;
   max-width: 10rem;
   width: 100%;
   }
 /* <dl> styles for screens larger than 576px */
 @media (min-width: 576px) {
   .panel-body dt {
     flex-basis: 20%;
   }
   .panel-body dd {
     flex-basis: 80%;
     margin: 0;
   }
 }
 /* <dl> styles for screens larger than 768px */
 @media (min-width: 768px) {
   .panel-body dt {
     flex-basis: 100%;
   }
   .panel-body dd {
     flex-basis: 100%;
     margin: 0 0 0 1.5rem;
   }
 }
 /* <dl> styles for screens larger than 992px */
 @media (min-width: 992px) {
   .panel-body dt {
     flex-basis: 25%;
   }
   .panel-body dd {
     flex-basis: 75%;
     margin: 0;
   }
 }
 /* <dl> styles for screens larger than 1200px */
 @media (min-width: 1200px) {
   .panel-body dt {
     flex-basis: 20%;
   }
   .panel-body dd {
     flex-basis: 80%;
     margin: 0;
   }
 }
 .collapsed-panel-body {
 max-height: 0;
 }
 .expanded-panel-body {
 max-height: 12rem;
 }
 
 
 /* <EmptySearch /> styles
 ************** */
 .empty-search {
   font-size: 125%;
   text-align: center;
 }
   .empty-search img {
     display: block;
     margin: 0 auto;
     max-height: auto;
     max-width: 18rem;
     width: 100%;
   }
   .empty-search h1 {
     margin: 0;
     padding: 0;
   }
   .empty-search p {
     margin: 0;
     padding: 0;
   }
 
 
 /* <Formula /> styles
 ************** */
 .formula {
   display: table;
   padding: .375rem .75rem;
 }
 .accuracy {
   color: WhiteSmoke;
   display: table-cell;
   font-weight: 700;
   width: auto;
 }
 .approximate {
   border-radius: .25rem;
   background-color: GoldenRod;
   font-size: .75rem;
   padding: .25rem .5rem;
 }
 .exact {
   border-radius: .25rem;
   background-color: YellowGreen;
   font-size: .75rem;
   padding: .25rem .5rem;
 }
 .calculation {
   display: table-cell;
   font-size: .75rem;
   padding: .25rem .5rem;
   width: auto;
 }
   .calculation span {
     font-weight: 700;
   }
 
 
 /* <footer> styles
  ************** */
 footer {
   background-color: Gainsboro;
   font-size: 1rem;
   padding: 1rem 0;
   width: 100%;
 }
   footer img {
     display: block;
     height: auto;
     margin: 0 auto;
     max-height: 3rem;
     max-width: 3rem;
     width: 100%;
   }